.elgens-header {
  background: #fff;
  position: sticky;
  top: 0;
  z-index: 1000;
  font-family: "IBM Plex Sans Condensed", sans-serif;
  /* ===== Logo ===== */
  /* ===== 手機漢堡 ===== */
  /* ===== 導覽基礎 ===== */
  /* ===== 第一層 ===== */
  /* ===== 次層選單（白底卡片樣式） ===== */
  /* ===== 手機子選單按鈕 ===== */
  /* ===== 桌機 hover 展開 (已修正) ===== */
  /* ===== 手機 Sheet ===== */
}
.elgens-header .container {
  max-width: 1320px;
  margin: 0 auto;
  padding: 10px 20px;
  display: flex;
  align-items: center;
  gap: 16px;
}
.elgens-header .classic {
  border-bottom: 2px solid #e6e6e6;
  position: relative;
}
.elgens-header .classic:after {
  content: "";
  position: absolute;
  left: 0;
  right: 0;
  bottom: -2px;
  height: 4px;
  background: var(--line);
  box-shadow: 0 2px 6px rgba(229, 139, 30, 0.35);
}
.elgens-header .brand {
  display: flex;
  align-items: center;
}
.elgens-header .brand img {
  height: 34px;
  width: auto;
  display: block;
}
.elgens-header .nav-toggle {
  margin-left: auto;
  display: inline-flex;
  flex-direction: column;
  gap: 5px;
  width: 44px;
  height: 36px;
  border: 1px solid #e5e7eb;
  background: #fff;
  border-radius: 8px;
  align-items: center;
  justify-content: center;
  cursor: pointer;
}
.elgens-header .nav-toggle .bar {
  width: 20px;
  height: 2px;
  background: #111;
}
.elgens-header .nav-toggle[aria-expanded=true] .bar:nth-child(2) {
  opacity: 0;
}
.elgens-header .nav-toggle[aria-expanded=true] .bar:nth-child(1) {
  transform: translateY(7px) rotate(45deg);
}
.elgens-header .nav-toggle[aria-expanded=true] .bar:nth-child(3) {
  transform: translateY(-7px) rotate(-45deg);
}
.elgens-header .elgens-nav {
  position: relative;
  margin-left: 20px;
  flex: 1;
  font-size: 19px; /* ✅ 導覽字體大小設定 */
}
.elgens-header .menu {
  list-style: none;
  margin: 0;
  padding: 0;
}
.elgens-header .menu .menu-item {
  display: flex;
}
.elgens-header .menu.level-1 {
  display: flex !important;
  align-items: center;
  justify-content: center;
  gap: 8px;
  padding: 0;
  margin: 0;
}
.elgens-header .menu.level-1 > .menu-item {
  display: flex !important;
  align-items: center;
  position: relative;
  padding: 0 8px;
  width: auto !important;
}
.elgens-header .menu.level-1 > .menu-item > a {
  display: inline-flex;
  align-items: center;
  gap: 6px;
  padding: 12px 10px;
  color: #222 !important;
  text-decoration: none;
  font-weight: 600;
  letter-spacing: 0.2px;
}
.elgens-header .menu.level-1 > .menu-item > a:hover {
  color: #0d0d0d !important;
}
.elgens-header .menu.level-1 > .menu-item.has-children > a::after {
  content: "▾";
  font-size: 0.8em;
  color: #9aa1ab !important;
  margin-left: 2px;
}
.elgens-header .menu.level-2,
.elgens-header .menu.level-3,
.elgens-header .menu.level-4,
.elgens-header .menu.level-5 {
  position: absolute;
  left: 0;
  top: 100%;
  min-width: 280px;
  background: #fff;
  border: 1px solid #eef2f6;
  border-radius: 12px;
  padding: 8px;
  display: none;
  box-shadow: var(--shadow);
  z-index: 1000;
}
.elgens-header .menu.level-3,
.elgens-header .menu.level-4,
.elgens-header .menu.level-5 {
  left: 100%;
  top: 0;
}
.elgens-header .menu.level-2 a,
.elgens-header .menu.level-3 a,
.elgens-header .menu.level-4 a,
.elgens-header .menu.level-5 a {
  display: block;
  padding: 10px 12px;
  color: #2b2f33 !important;
  text-decoration: none;
  border-radius: 8px;
  font-weight: 500;
}
.elgens-header .menu.level-2 a:hover,
.elgens-header .menu.level-3 a:hover,
.elgens-header .menu.level-4 a:hover,
.elgens-header .menu.level-5 a:hover {
  background: #f6f8fb;
  color: #111 !important;
}
.elgens-header .menu.level-2 > .menu-item {
  position: relative;
}
.elgens-header .menu.level-2 > .menu-item.has-children > a::after {
  content: "▸";
  color: #a0a7b2;
  margin-left: 0.4em;
  font-size: 0.9em;
}
.elgens-header .submenu-toggle {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  background: transparent;
  border: 0;
  color: #9aa1ab;
  cursor: pointer;
  font-size: 14px;
  line-height: 1;
  padding: 0 2px;
}
.elgens-header .menu .has-children > .menu {
  display: none;
}
@media (min-width: 992px) {
  .elgens-header .nav-toggle {
    display: none;
  }
  .elgens-header .menu.level-1 {
    display: flex !important;
  }
  .elgens-header .menu.level-1 > .menu-item:hover > .menu {
    display: block;
  }
  .elgens-header .menu.level-2 > .menu-item:hover > .menu {
    display: block;
  }
  .elgens-header .menu.level-3 > .menu-item:hover > .menu {
    display: block;
  }
  .elgens-header .menu.level-4 > .menu-item:hover > .menu {
    display: block;
  }
  .elgens-header .submenu-toggle {
    display: none;
  }
}
@media (max-width: 991.98px) {
  .elgens-header .elgens-nav {
    flex: unset;
    width: 100%;
  }
  .elgens-header #elgens-menu {
    position: absolute;
    top: 100%;
    left: 0;
    right: 0;
    background: #fff;
    border-top: 1px solid #eee;
    display: none;
    max-height: calc(100vh - 70px);
    overflow-y: auto;
  }
  .elgens-header #elgens-menu.open {
    display: block;
  }
  .elgens-header .menu.level-1 {
    display: block !important;
    padding: 8px;
  }
  .elgens-header .menu.level-1 > .menu-item {
    display: block;
    border-bottom: 1px solid #f1f3f5;
  }
  .elgens-header .menu.level-1 > .menu-item > a {
    display: flex;
    justify-content: space-between;
  }
  .elgens-header .menu.level-2,
  .elgens-header .menu.level-3,
  .elgens-header .menu.level-4,
  .elgens-header .menu.level-5 {
    position: static;
    border: 0;
    border-radius: 0;
    box-shadow: none;
    padding: 0;
  }
  .elgens-header .menu.level-2 a,
  .elgens-header .menu.level-3 a,
  .elgens-header .menu.level-4 a,
  .elgens-header .menu.level-5 a {
    padding: 12px 16px;
  }
  .elgens-header .menu.level-2 {
    background: #fafbfc;
    padding-left: 10px;
  }
  .elgens-header .menu.level-3 {
    background: #f7f9fb;
    padding-left: 10px;
  }
  .elgens-header .menu.level-4 {
    background: #f5f7fa;
    padding-left: 10px;
  }
  .elgens-header .menu.level-5 {
    background: #f3f6fa;
    padding-left: 10px;
  }
}
.elgens-header a:hover {
  color: #0d0d0d;
}

