@mixin btn-corner($color) {
  &::before,
  &::after,
  .c-btn-ghost__deco::before,
  .c-btn-ghost__deco::after {
    content: "";
    display: inline-block;
    position: absolute;
    opacity: 0;
    width: 8px;
    height: 8px;
    border-color: $color;
  }

  &::before {
    border-top: solid 1px $color;
    border-left: solid 1px $color;
    left: 0;
    top: 0;
  }

  &::after {
    border-top: solid 1px $color;
    border-right: solid 1px $color;
    top: 0;
    right: 0;
  }

  .c-btn-ghost__deco::before {
    border-bottom: solid 1px $color;
    border-left: solid 1px $color;
    bottom: 0;
    left: 0;
  }

  .c-btn-ghost__deco::after {
    border-bottom: solid 1px $color;
    border-right: solid 1px $color;
    bottom: 0;
    right: 0;
  }
}

.c-btn-feature {
  position: relative;

  &:hover {
    color: $brand;

    &::before,
    &::after,
    .c-btn-ghost__deco::before,
    .c-btn-ghost__deco::after {
      opacity: 1;
    }
  }

  &--orange {
    @include btn-corner($brand);
    transition: all $base-animation;

    &:hover {
      background: url(../images/home/button-dotted-orange.svg), rgba($dark, 0.3);
    }
  }

  &--black {
    @include btn-corner($dark);
    background: $dark;
    color: $white;
    transition: all $base-animation;

    &:hover {
      background: url(../images/home/button-dotted-black.svg);
      color: $dark;
    }
  }

  &--dark {
    @include btn-corner($brand);
    background: $brand;
    color: $dark;
    transition: all $base-animation;

    &:hover {
      background: url(../images/home/button-dotted-dark.svg), rgba($dark, 0.3);
    }
  }
}

.c-btn-ghost {
  display: flex;
  align-items: center;
  @include btn-corner($brand);
  position: relative;
  color: $font-secondary;
  transition: all $base-animation;

  &:hover:not(.disabled):not(:disabled) {
    color: $brand;

    &::before,
    &::after,
    .c-btn-ghost__deco::before,
    .c-btn-ghost__deco::after {
      opacity: 1;
    }
  }

  &.disabled,
  &:disabled {
    opacity: 0.5;
  }
}
