.elgens-header {
  background: #fff;
  position: sticky;
  top: 0;
  z-index: 1000;
  font-family: 'IBM Plex Sans Condensed', sans-serif;

  .container {
    max-width: 1320px;
    margin: 0 auto;
    padding: 10px 20px;
    display: flex;
    align-items: center;
    gap: 16px;
  }

  .classic {
    border-bottom: 2px solid #e6e6e6;
    position: relative;
  }

  .classic:after {
    content: "";
    position: absolute;
    left: 0;
    right: 0;
    bottom: -2px;
    height: 4px;
    background: var(--line);
    box-shadow: 0 2px 6px rgba(229, 139, 30, .35);
  }

  /* ===== Logo ===== */
  .brand {
    display: flex;
    align-items: center;

    img {
      height: 34px;
      width: auto;
      display: block;
    }
  }

  /* ===== 手機漢堡 ===== */
  .nav-toggle {
    margin-left: auto;
    display: inline-flex;
    flex-direction: column;
    gap: 5px;
    width: 44px;
    height: 36px;
    border: 1px solid #e5e7eb;
    background: #fff;
    border-radius: 8px;
    align-items: center;
    justify-content: center;
    cursor: pointer;

    .bar {
      width: 20px;
      height: 2px;
      background: #111;
    }
  }

  .nav-toggle[aria-expanded="true"] .bar:nth-child(2) {
    opacity: 0;
  }

  .nav-toggle[aria-expanded="true"] .bar:nth-child(1) {
    transform: translateY(7px) rotate(45deg);
  }

  .nav-toggle[aria-expanded="true"] .bar:nth-child(3) {
    transform: translateY(-7px) rotate(-45deg);
  }

  /* ===== 導覽基礎 ===== */
  .elgens-nav {
    position: relative;
    margin-left: 20px;
    flex: 1;
    font-size: 19px; /* ✅ 導覽字體大小設定 */
  }

  .menu {
    list-style: none;
    margin: 0;
    padding: 0;

    .menu-item {
      display: flex;
    }
  }

  /* ===== 第一層 ===== */
  .menu.level-1 {
    display: flex !important;
    align-items: center;
    justify-content: center;
    gap: 8px;
    padding: 0;
    margin: 0;
  }

  .menu.level-1 > .menu-item {
    display: flex !important;
    align-items: center;
    position: relative;
    padding: 0 8px;
    width: auto !important;
  }

  .menu.level-1 > .menu-item > a {
    display: inline-flex;
    align-items: center;
    gap: 6px;
    padding: 12px 10px;
    color: #222 !important;
    text-decoration: none;
    font-weight: 600;
    letter-spacing: .2px;
  }

  .menu.level-1 > .menu-item > a:hover {
    color: $dark !important;
  }

  .menu.level-1 > .menu-item.has-children > a::after {
    content: "▾";
    font-size: .8em;
    color: #9aa1ab !important;
    margin-left: 2px;
  }

  /* ===== 次層選單（白底卡片樣式） ===== */
  .menu.level-2,
  .menu.level-3,
  .menu.level-4,
  .menu.level-5 {
    position: absolute;
    left: 0;
    top: 100%;
    min-width: 280px;
    background: #fff;
    border: 1px solid #eef2f6;
    border-radius: 12px;
    padding: 8px;
    display: none;
    box-shadow: var(--shadow);
    z-index: 1000;
  }

  .menu.level-3,
  .menu.level-4,
  .menu.level-5 {
    left: 100%;
    top: 0;
  }

  .menu.level-2 a,
  .menu.level-3 a,
  .menu.level-4 a,
  .menu.level-5 a {
    display: block;
    padding: 10px 12px;
    color: #2b2f33 !important;
    text-decoration: none;
    border-radius: 8px;
    font-weight: 500;
  }

  .menu.level-2 a:hover,
  .menu.level-3 a:hover,
  .menu.level-4 a:hover,
  .menu.level-5 a:hover {
    background: #f6f8fb;
    color: #111 !important;
  }

  .menu.level-2 > .menu-item {
    position: relative;
  }

  .menu.level-2 > .menu-item.has-children > a::after {
    content: "▸";
    color: #a0a7b2;
    margin-left: .4em;
    font-size: .9em;
  }

  /* ===== 手機子選單按鈕 ===== */
  .submenu-toggle {
    display: inline-flex;
    align-items: center;
    justify-content: center;
    background: transparent;
    border: 0;
    color: #9aa1ab;
    cursor: pointer;
    font-size: 14px;
    line-height: 1;
    padding: 0 2px;
  }

  .menu .has-children > .menu {
    display: none;
  }

  /* ===== 桌機 hover 展開 (已修正) ===== */
  @media (min-width: 992px) {
    .nav-toggle {
      display: none;
    }

    .menu.level-1 {
      display: flex !important;
    }

    .menu.level-1 > .menu-item:hover > .menu {
      display: block;
    }

    .menu.level-2 > .menu-item:hover > .menu {
      display: block;
    }

    .menu.level-3 > .menu-item:hover > .menu {
      display: block;
    }

    .menu.level-4 > .menu-item:hover > .menu {
      display: block;
    }

    .submenu-toggle {
      display: none;
    }
  }

  /* ===== 手機 Sheet ===== */
  @media (max-width: 991.98px) {
    .elgens-nav {
      flex: unset;
      width: 100%;
    }

    #elgens-menu {
      position: absolute;
      top: 100%;
      left: 0;
      right: 0;
      background: #fff;
      border-top: 1px solid #eee;
      display: none;
      max-height: calc(100vh - 70px);
      overflow-y: auto;
    }

    #elgens-menu.open {
      display: block;
    }

    .menu.level-1 {
      display: block !important;
      padding: 8px;
    }

    .menu.level-1 > .menu-item {
      display: block;
      border-bottom: 1px solid #f1f3f5;
    }

    .menu.level-1 > .menu-item > a {
      display: flex;
      justify-content: space-between;
    }

    .menu.level-2,
    .menu.level-3,
    .menu.level-4,
    .menu.level-5 {
      position: static;
      border: 0;
      border-radius: 0;
      box-shadow: none;
      padding: 0;
    }

    .menu.level-2 a,
    .menu.level-3 a,
    .menu.level-4 a,
    .menu.level-5 a {
      padding: 12px 16px;
    }

    .menu.level-2 {
      background: #fafbfc;
      padding-left: 10px;
    }

    .menu.level-3 {
      background: #f7f9fb;
      padding-left: 10px;
    }

    .menu.level-4 {
      background: #f5f7fa;
      padding-left: 10px;
    }

    .menu.level-5 {
      background: #f3f6fa;
      padding-left: 10px;
    }
  }

  a:hover {
    color: $dark;
  }
}
