// Override variables
// $enable-dark-mode: true;
$enable-rounded: false;
$enable-rfs: false;
// $min-contrast-ratio: 3;

$gray-100: #f2f2f2;
$gray-200: #e0e0e0;
$gray-300: #bfbfbf;
$gray-500: #757575;
$gray-700: #4d4d4d;
$gray-800: #212121;
$gray-900: #0d0d0d;

$white: #fff;
$white-70: rgba($white, 0.7);
$white-50: rgba($white, 0.5);
$white-30: rgba($white, 0.3);
$white-20: rgba($white, 0.2);
$white-10: rgba($white, 0.1);

$orange-700: #a75300;
$orange-500: #ee7700;
$orange-300: #f18e2b;

$blue-500: #0b1b53;

$primary: $orange-500;
$secondary: $white;
$danger: #e60012;
$dark: $gray-900;

$body-color: $white;
$body-bg: $gray-900;

// Custom Colors
$accent: $blue-500;
$tertiary: $white-50;
$brand: $orange-500;

$font-primary: $white;
$font-secondary: $white-70;
$font-tertiary: $white-50;
$font-brand: $orange-500;
$font-onLight: $gray-900;

$stroke-primary: $white;
$stroke-secondary: $gray-500;
$stroke-tertiary: $gray-700;
$stroke-onLight: $gray-900;
$stroke-brand: $orange-500;
// $light-blue: #7989b8;

// Custom Animation
$base-animation: ease-out 0.3s;
$slow-animation: cubic-bezier(0.15, 0.85, 0.15, 1) 0.6s;

// Fonts
$main-font: "IBM Plex Sans Condensed", sans-serif;

$display-font-weight: 600;
$display-line-height: 1.1;

$font-family-sans-serif: $main-font;

$font-size-base: 1rem;
$h1-font-size: $font-size-base * 3;
$h2-font-size: $font-size-base * 2.5;
$h3-font-size: $font-size-base * 2;
$h4-font-size: $font-size-base * 1.5;
$h5-font-size: $font-size-base * 1.25;
$h6-font-size: $font-size-base * 1.125;

$link-decoration: none;
$paragraph-margin-bottom: 0;
$headings-margin-bottom: 0;

// Buttons
$btn-padding-y: 0.75rem;
$btn-padding-x: 1.5rem;
$btn-font-size: $font-size-base * 1.125;
$btn-line-height: 1.4;

$btn-padding-y-sm: 0.5rem;
$btn-padding-x-sm: 2rem;
$btn-font-size-sm: $font-size-base;

$btn-padding-y-lg: 1rem;
$btn-padding-x-lg: 3rem;
$btn-font-size-lg: $font-size-base * 1.25;

// Form
$input-focus-box-shadow: none;
$form-select-focus-box-shadow: none;

$form-valid-border-color: none;
$form-invalid-border-color: none;

$form-feedback-margin-top: 1.25rem;
$form-feedback-font-size: 1rem;

$form-feedback-icon-valid: none;
$form-feedback-icon-invalid: none;

// Pagination
$pagination-color: rgba($dark, 0.4);
$pagination-border-width: 0;

$pagination-active-color: $primary;
$pagination-active-bg: transparent;
$pagination-active-border-color: transparent;

@import "./node_modules/bootstrap/scss/functions";
@import "./node_modules/bootstrap/scss/variables";
@import "./node_modules/bootstrap/scss/mixins";
@import "./node_modules/bootstrap/scss/maps";

$spacers: map-merge(
    $spacers,
    (
      6px: $spacer * 0.375,
      10px: $spacer * 0.625,
      12px: $spacer * 0.75,
      14px: $spacer * 0.875,
      20px: $spacer * 1.25,
      28px: $spacer * 1.75,
      32px: $spacer * 2,
      36px: $spacer * 2.375,
      40px: $spacer * 2.5,
      60px: $spacer * 3.75,
      80px: $spacer * 5,
    )
);

$font-sizes: map-merge(
    $font-sizes,
    (
      1: $font-size-base * 5,
      //80
      2: $font-size-base * 3.75,
      //60
      3: $h1-font-size,
      //48
      4: $h2-font-size,
      //40
      5: $font-size-base * 2.25,
      //36
      6: $h3-font-size,
      //32
      7: $font-size-base * 1.75,
      //28
      8: $h4-font-size,
      //24
      9: $font-size-base * 1.375,
      //22
      10: $h5-font-size,
      //20
      11: $h6-font-size,
      //18
      12: $font-size-base,
      //16
      13: $font-size-base * 0.875,
      //14
    )
);

$utilities: (
  "lh": (
    property: line-height,
    class: lh,
    values: (
      180: 1.8,
      190: 1.9,
      200: 2,
    ),
  ),
);

$utilities: map-merge(
    $utilities or (),
    (
      "font-size":
      map-merge(
          map-get($utilities, "font-size") or (),
          (
            property: font-size,
            class: fs,
            values:
            map-merge(
                $font-sizes,
                  ()
            ),
            responsive: true,
          )
      ),
    )
);
