form {
  --bs-form-invalid-color: #e60012;
}

.was-validated .form-control:invalid:focus,
.form-control.is-invalid:focus {
  border-color: none;
  box-shadow: none;
}

.was-validated .form-control:valid:focus,
.form-control.is-valid:focus {
  border-color: none;
  box-shadow: none;
}

.c-form-group {
  margin-bottom: 12px;
  position: relative;

  .invalid-feedback {
    font-size: 0.875rem;
    margin-top: 8px;

    span {
      font-size: 1.25rem;
      vertical-align: sub;
    }
  }

  label {
    color: white;
    padding-left: 0;
    padding: 1.2rem 0.75rem;
  }

  & > .form-control {
    border: none;
    padding-left: 4px;
    border-bottom: solid 1px $stroke-tertiary;
    height: 68px;
    font-size: 1.125rem;
    background-color: transparent;

    &:focus {
      border-bottom: solid 1px $brand;
    }

    &.disabled,
    &:disabled {
      color: $font-tertiary;
    }
  }

  & > label {
    padding-left: 0;
    font-size: 1.125rem;
  }

  &::after {
    content: "*";
    color: $primary;
    position: absolute;
    top: 20px;
    left: -8px;
  }
}

.ts-control,
.ts-wrapper.single.input-active .ts-control {
  background: none;
}

.ts-control {
  color: white !important;
  border: none;
  border-bottom: solid 1px $stroke-tertiary;
  padding-left: 2px;
  padding-bottom: 12px;
  margin-bottom: 12px;
  font-size: 1.125rem;
  border-radius: 0;
  height: 68px;
  align-items: center;

  input {
    font-size: 1.125rem;
  }

  &:focus {
    border-bottom: solid 1px $brand;
  }

  &.disabled,
  &:disabled {
    color: $font-tertiary;
  }

  input::-webkit-input-placeholder {
    color: white !important;
  }
}

.ts-dropdown {
  background: $gray-800;
  color: white;
  border: none;
  font-size: 1.125rem;
}

.ts-dropdown .option {
  padding: 12px 20px;

  &:hover {
    color: $brand;
    background: none;
  }
}

.ts-dropdown .active {
  background: none;
  color: $brand;
}

.full .ts-control {
  background: none;
}

.ts-wrapper:not(.form-control, .form-select).single .ts-control {
  background-image: url("data:image/svg+xml,%3csvg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 16 16'%3e%3cpath fill='none' stroke='%23343a40' stroke-linecap='round' stroke-linejoin='round' stroke-width='2' d='m2 5 6 6 6-6'/%3e%3c/svg%3e");
  background-position: right 0.75rem center;
  background-repeat: no-repeat;
  background-size: 16px 12px;
}
