.view-home {
  font-size: 18px;

  .c-btn-feature {
    max-width: 260px;
    display: flex;
    justify-content: center;
    align-items: center;
    padding-left: 0;
    padding-right: 0;
  }

  .text-center > .lineParent {
    margin-left: auto;
    margin-right: auto;
  }

  .lineParent {
    overflow: hidden;
    max-width: max-content;
  }

  .bg-squares {
    background-image: linear-gradient(
        90deg,
        rgba(255, 255, 255, 0.1) 1px,
        transparent 1px
    ),
    linear-gradient(rgba(255, 255, 255, 0.1) 1px, transparent 1px);
    background-size: 100px 100px;
  }

  .l-section--banner {
    .c-video-wrapper {
      position: relative;

      &::before {
        content: "";
        width: 100%;
        height: 100%;
        position: absolute;
        top: 0;
        left: 0;
        background: linear-gradient(rgba($gray-900, 0%), rgba($gray-900, 100%));
      }
    }
  }

  .l-section--services {
    .card {
      color: white;
      background-color: $gray-800;
      outline: solid 1px $stroke-tertiary;
      border: 0;
      position: relative;
      z-index: 0;

      &::before {
        content: "";
        width: 100%;
        height: 100%;
        position: absolute;
        top: 0;
        left: 0;
        background-image: url(../images/home/services-deco.png);
        background-size: cover;
        background-position: center;
        z-index: -1;
        opacity: 0;
        transition: opacity $base-animation;

        //@include media-breakpoint-down(lg) {
        //  background-image: url(../images/home/services-deco-mobile.png);
        //}
      }

      &:hover {
        &::before {
          opacity: 1;
        }

        img {
          transform: scale(1.1);
        }
      }
    }

    .c-card-img {
      overflow: hidden;

      img {
        transition: transform $base-animation;
      }
    }

    .row {
      background: $gray-900;
    }

    .col-lg-12 .c-card-img {
      width: 31.1%;
      @include media-breakpoint-down(lg) {
        width: 100%;
      }
    }

    .c-link-more {
      display: flex;
      justify-content: flex-end;
      align-items: center;
      color: $font-tertiary;

      &__icon {
        color: $white;
        width: 60px;
        height: 60px;
        display: flex;
        justify-content: center;
        align-items: center;
        transition: all $base-animation;
      }

      &:hover {
        color: $font-brand;

        .c-link-more__icon {
          margin-inline-start: 12px;
          background: $white-10;
          color: $font-brand;
        }
      }
    }
  }

  .l-section--products {
    background: url(../images/home/products-bg.svg), $orange-500;

    ul {
      padding-left: 1.5rem;
    }

    .c-feature-img {
      max-width: 300px;
      width: 40.2%;

      @include media-breakpoint-down(lg) {
        max-width: initial;
        width: 100%;
      }
    }

    .js-product-container {
      position: relative;
    }

    .js-product-slider {
      position: absolute;
      opacity: 0;
      @include media-breakpoint-down(lg) {
        position: static;
      }

      img {
        max-width: 400px;
      }

      &:first-child {
        opacity: 1;
      }
    }

    .c-btn-feature--sliders {
      color: $dark;
      outline: solid 1px $dark;
      outline-offset: -1px;
      background: none;

      span {
        position: relative;
        width: 100%;

        &:after, &:before {
          content: "";
          display: inline-block;
          position: absolute;
          opacity: 0;
          width: 8px;
          height: 8px;
          border-color: $dark;
        }

        &::before {
          border-bottom: solid 1px $dark;
          border-left: solid 1px $dark;
          bottom: -12px;
          left: 0;
        }

        &::after {
          border-bottom: solid 1px $dark;
          border-right: solid 1px $dark;
          bottom: -12px;
          right: 0;
        }
      }

      @include media-breakpoint-down(lg) {
        width: 100%;
        max-width: none;
      }

      &:hover,
      &.active {
        outline: solid 1px transparent;
        outline-offset: -1px;
        border: 0;
        background: url(../images/home/button-dotted-black.svg);

        &::before,
        &::after,
        & span::before,
        & span::after {
          opacity: 1;
        }
      }
    }
  }

  .l-section--cases {
    .card {
      color: white;
      background-color: $gray-800;
      min-height: 466px;
      position: relative;
    }

    .c-feature-deco {
      position: relative;
      display: inline-block;
      padding: 8px 20px;

      &::before,
      &::after,
      span::before,
      span::after {
        content: "";
        display: inline-block;
        position: absolute;
        width: 8px;
        height: 8px;
        color: $brand;
      }

      &::before {
        border-top: solid 1px $brand;
        border-left: solid 1px $brand;
        left: 0;
        top: 0;
      }

      &::after {
        border-top: solid 1px $brand;
        border-right: solid 1px $brand;
        top: 0;
        right: 0;
      }

      span::before {
        border-bottom: solid 1px $brand;
        border-left: solid 1px $brand;
        bottom: 0;
        left: 0;
      }

      span::after {
        border-bottom: solid 1px $brand;
        border-right: solid 1px $brand;
        bottom: 0;
        right: 0;
      }
    }
  }

  .l-section--certified {
    background-image: url(../images/home/certified-bg.png);
    background-size: cover;
    background-repeat: no-repeat;
    @include media-breakpoint-down(lg) {
      background-image: url(../images/home/certified-bg-mobile.png);
    }

    .tab-pane {
      display: none; /* 預設隱藏 */
      opacity: 0;
      transform: translateY(60px);
    }

    .tab-pane.active.show {
      display: block;
      animation: fadeUp $slow-animation forwards;
    }

    @keyframes fadeUp {
      from {
        opacity: 0;
        transform: translateY(30px);
      }
      to {
        opacity: 1;
        transform: translateY(0);
      }
    }

    .c-btn-feature--sliders {
      outline: solid 1px $stroke-tertiary;
      border: none;
      @include media-breakpoint-down(lg) {
        max-width: none;
      }

      span {
        position: relative;
        width: 100%;

        &:after, &:before {
          content: "";
          display: inline-block;
          position: absolute;
          opacity: 0;
          width: 8px;
          height: 8px;
          border-color: $primary;
        }

        &::before {
          border-bottom: solid 1px $primary;
          border-left: solid 1px $primary;
          bottom: -12px;
          left: 0;
        }

        &::after {
          border-bottom: solid 1px $primary;
          border-right: solid 1px $primary;
          bottom: -12px;
          right: 0;
        }
      }

      &:hover,
      &.active {
        outline: solid 1px transparent;
        border: 0;
        color: $brand;
        background: url(../images/home/button-dotted-orange.svg), $gray-800;
        background-color: $gray-800;

        &::before,
        &::after,
        & span::before,
        & span::after {
          opacity: 1;
        }
      }
    }

    .l-content__container {
      height: 450px;
      @include media-breakpoint-down(lg) {
        height: auto;
      }
    }


    .js-btn-prev,
    .js-btn-next {
      width: 40px;
      height: 40px;
      display: flex;
      align-items: center;
      justify-content: center;
      color: $white;
    }

    .c-nav-item {
      @include media-breakpoint-down(lg) {
        width: 50%;
        &:last-child,
        &:last-child button {
          width: 100%;
          max-width: none;
        }
      }

      & + .c-nav-item {
        margin-left: 1px;
        @include media-breakpoint-down(lg) {
          margin-left: 0;
        }
      }
    }

    .c-pagination > * {
      opacity: 0;
      transform: translateY(60px);
      transition: opacity $slow-animation, transform $slow-animation;
    }

    .c-pagination > *.show {
      opacity: 1;
      transform: translateY(0);
    }

    .c-feature-item {
      opacity: 0;
      transform: translateY(60px);
      transition: opacity 0.3s ease, transform 0.3s ease;
      display: none;
      margin-bottom: 1px;

      > * {
        transition: all $base-animation;
      }

      a {
        color: $font-onLight;
      }

      &:hover {
        background: $dark;

        a {
          color: $white;
        }

        .c-link-more {
          color: $brand;

          &__icon {
            margin-left: 12px;
            color: $brand;
            background: $white-10;
          }
        }
      }
    }

    .c-feature-item.show {
      display: flex;
      opacity: 1;
      transform: translateY(0);
    }

    .c-feature-item,
    .c-pagination > * {
      transition: opacity 0.3s ease, transform 0.3s ease;
    }

    .c-feature-item.show,
    .c-pagination > *.show {
      opacity: 1;
    }

    .c-pagination {
      .btn {
        border: 0;
      }

      &.btn.disabled,
      &.btn:disabled {
        opacity: 0.5;
      }
    }

    .c-btn-feature--download {
      background: $brand;
      color: $dark;

      &:hover {
        background: url(../images/home/button-dotted-orange.svg);
        color: $brand;
      }
    }

    .c-feature-img {
      background: url(../images/home/certified-slider-bg.png), $brand;
      padding: 32px 0;
      background-size: cover;
      display: flex;
      justify-content: center;
      align-items: center;
      width: 34.25%;
      @include media-breakpoint-down(lg) {
        width: 100%;
        min-height: 351px;
      }

      img {
        max-width: 260px;
      }

      .c-feature-img__text {
        max-width: 360px;

      }
    }

    .c-feature-item {
      outline: solid 1px $stroke-onLight;
      padding: 8px 20px;

      .c-link-more__icon {
        width: 60px;
        height: 60px;
        display: flex;
        align-items: center;
        justify-content: center;
        color: $white;
      }
    }
  }

  .l-section--why {
    position: relative;

    &::before {
      content: "";
      background-image: url(../images/home/why-bg.svg);
      display: inline-block;
      position: absolute;
      bottom: 0;
      right: 0;
      height: 1440px;
      width: 720px;
      z-index: -1;
    }

    .c-article {
      font-size: 1.5rem;
      line-height: 1.4;
      font-weight: 600;

      p + p {
        margin-top: 2rem;
      }
    }

    .l-number-list {
      color: $dark;
      background: $orange-500;

      .c-number {

        @include media-breakpoint-up(lg) {
          font-size: 5rem;
        }
      }
    }
  }

  .l-section--solutions {
    .card {
      color: white;
      background-color: $gray-800;
    }

    .c-feature-img {
      max-width: 100px;
    }
  }

  .l-section--presentation {
    background-image: url(../images/home/presentation-bg.png);
    background-repeat: no-repeat;
    background-size: cover;
  }

  .l-section--video {
    .c-feature-content {
      border: solid 1px $stroke-tertiary;
      background-image: url(../images/home/test-video-bg.png);
      background-repeat: no-repeat;
      background-size: cover;
    }

    .c-feature-desc {
      width: 59%;
      @include media-breakpoint-down(lg) {
        width: 100%;
      }
    }

    .c-feature-img {
      width: 41%;
      @include media-breakpoint-down(lg) {
        width: 100%;
      }
    }
  }

  .l-section--brochure {
    background-image: url(../images/home/brochure-bg.svg);

    .c-feature-img {
      transform: translateX(84px);

      @include media-breakpoint-down(lg) {
        transform: translateX(0px);
      }
    }
  }

  .l-section--contact {
    background-image: url(../images/home/contact-bg.png);
    background-repeat: no-repeat;
    background-size: cover;
    @include media-breakpoint-down(lg) {
      padding-bottom: 94px;
    }
  }
}
