.l-float-tools {
  background: rgba(13, 13, 13, 0.4);
  backdrop-filter: blur(10px);
  -webkit-backdrop-filter: blur(10px);
  outline: solid 1px $stroke-tertiary;
  display: flex;
  align-items: center;

  position: fixed;
  right: 20px;
  bottom: 20px;
  z-index: 50;

  @include media-breakpoint-down(lg) {
    right: 12px;
  }

  .c-float-button {
    display: flex;
    width: 60px;
    height: 60px;
    border-radius: 100px;
    font-size: 24px;
    align-items: center;
    justify-content: center;
  }

  .btn-primary {
    position: relative;
    border: 0;
    @include btn-corner($brand);

    &:hover {
      background: url(../images/home/button-dotted-orange.svg), rgba($dark, 0.3);
      color: $brand;

      &::before,
      &::after,
      span::before,
      span::after {
        opacity: 1;
      }
    }
  }
}

.l-main-footer {
  font-family: Inter, sans-serif;
  font-size: 0.875rem;

  .c-copyright {
    position: relative;
    padding-right: 1.5rem;

    &::before {
      content: "";
      width: 4px;
      height: 4px;
      display: inline-block;
      background: $font-tertiary;
      position: absolute;
      right: 8px;
      top: 50%;
      border-radius: 50%;
      transform: translateY(-50%);
      @include media-breakpoint-down(lg) {
        display: none;
      }
    }
  }
}
